<template>
  <section>
    <header>
      <h1 class="title"><i class="fal fa-rss" style="color: orange;"></i> 个人博客</h1>
      <h3 class="title"><i class="fal fa-pencil-alt" style="color: red;"></i> 最新文章</h3>
    </header>

    <section class="carousel">
      <div class="carousel--main">
        <div class="carousel--bg">
          <template v-for="(post, index) in posts">
            <img :key="post.id" :src="`https://www.its-silver.com${post.thumb}`" alt="" :class="index === currentPost ? 'show' : 'hidden'" />
          </template>
        </div>
        <div class="carousel--desc" :class="{ 'carousel--desc_hidden': currentPost !== index }" v-for="(post, index) in posts" :key="post.id">
          <div class="wrapper" :class="carouselStatus(index)">
            <h1 class="title">
              <a :href="post.href">{{ post.title || '' }}</a>
            </h1>
            <div class="ab-positioned">
              <p class="desc">
                category: <a :href="post.cateHref">{{ post.cate || '' }}</a>
              </p>
              <BaseButton :link="post.href" iconclass="fal fa-chevron-right" color="#e94e77" background="white" style="border: 1px dashed #e94e77;">Read More</BaseButton>
            </div>
          </div>
        </div>
      </div>
      <div class="carousel--aside">
        <div class="carousel--item" v-for="(post, index) in posts" :key="post.id">
          <div class="wrapper" :class="currentPost === index ? 'hover' : ''" @mouseenter="mouseEnter(post)" @mouseleave="mouseLeave">
            <time :datetime="post.date">{{ getDate(post) }}</time>
            <a class="title" :href="post.href">{{ post.title }}</a>
            <a class="desc" :href="post.cateHref">{{ post.cate }}</a>
          </div>
        </div>
      </div>
    </section>
  </section>
</template>

<script>
import { clearTimeout, setTimeout, setInterval, clearInterval } from 'timers'
export default {
  props: {
    posts: {
      type: Array,
      default: function() {
        return [
          {
            title: '',
            cate: '',
            href: '',
            cateHref: ''
          }
        ]
      }
    }
  },
  data() {
    return {
      currentPost: 0,
      /* 鼠标移入时间 */
      timer: null,
      delay: 100,
      /* 记录desc状态 */
      loopDelay: 8000,
      /* 记录轮播setInterval */
      intervalTimer: null
    }
  },
  mounted() {
    this.loop()
  },
  methods: {
    getDate(post) {
      const date = post.date || new Date()
      return `${date.getFullYear()}-${date.getMonth() + 1}-${date.getDate()}`
    },
    /* mouseenter */
    mouseEnter(post) {
      this.timer = setTimeout(() => {
        this.currentPost = post.id
      }, this.delay)
      /* 清除掉interval */
      clearInterval(this.intervalTimer)
    },
    /* mouseleave */
    mouseLeave() {
      this.loop()
      clearTimeout(this.timer)
    },
    carouselStatus(index) {
      return this.currentPost === index ? 'carousel--wrap_show' : 'carousel--wrap_hidden'
    },
    /* 图片轮播 */
    loop() {
      this.intervalTimer = setInterval(() => {
        this.increaseIndex()
      }, this.loopDelay)
    },
    increaseIndex() {
      this.currentPost = (this.currentPost + 1) % this.posts.length
    },
    decreaseIndex() {
      this.currentPost = this.currentPost - 1 < 0 ? this.posts.length : this.currentPost
      this.currentPost -= 1
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@/scss/responsive.scss';
$max-line-number: 3;
$small-text-color: #86a8e7;
$text-color: #2c5364;
$text-hover-color: #434343;
$text-underline-color: #2c5364;
$dash-border-color: #b12a5b;

$box-shadow-color: #e2d1c3;
$background-hover-color: #fdfdfb;

$desc-transition: 0.5s ease-in;
$desc-max-height: 15em;

$image-border-radius: 10px;
$aside-border-radius: 5px;
$liner-gradient: linear-gradient(135deg, #fdfcfb 0%, #e2d1c3 100%);

$margin: 80px;
$height: 580px;
@mixin shadow {
  box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.3), 5px 5px 15px rgba(0, 0, 0, 0.1), 5px 5px 20px rgba(0, 0, 0, 0.05);
}

header {
  h1.title {
    padding: 20px 0;
    margin-top: 1em;
    margin-bottom: 1em;
    border-bottom: 2px solid $text-underline-color;
  }
  h3.title {
    padding: 15px 0;
    border-bottom-right-radius: 50px;
    margin-right: calc(#{$margin} + 50%);
    border-bottom: 2px solid $text-underline-color;
  }
}

.carousel {
  display: flex;

  .show {
    display: block;
  }
  .hidden {
    display: none;
  }
  .carousel--main {
    @include shadow();
    height: $height;
    margin-top: $margin;
    margin-right: $margin;
    margin-bottom: $desc-max-height;
    border-radius: $image-border-radius;
    flex: 3.5 2;
    position: relative;
    font-size: 12px;
    padding: 5px;
    display: flex;
    .title,
    .desc {
      margin: 1em 0;
      text-transform: uppercase;
    }
    .title {
      font-size: 1.5em;
      font-weight: bolder;
      font-style: italic;

      a {
        text-decoration: none;
        color: white;
        border-bottom: 1px dashed $text-underline-color;
      }
    }

    .carousel--bg {
      // height: $height;
      overflow: hidden;
      width: 100%;
      height: 100%;
      img {
        width: 100%;
        height: 100%;
        object-fit: scale-down;
      }
      &::before {
        // position: absolute;
        display: block;
        content: '';
        left: -2px;
        right: -2px;
        top: -2px;
        bottom: -2px;
        background-image: linear-gradient(to top, #cfd9df 0%, #e2ebf0 100%);
      }
    }
    .carousel--desc {
      margin-top: 10px;
      color: white;
      position: absolute;
      // border-radius: 0 0 $image-border-radius $image-border-radius;
      top: 100%;
      left: 0px;
      right: 0px;
      // transform: translateY(-50%);
      padding: 1em 10vw 2em 1.5em;
      // padding-bottom: $padding-bottom;
      background-image: linear-gradient(to right, #ff8177 0%, #ff867a 0%, #ff8c7f 21%, #f99185 52%, #cf556c 78%, #b12a5b 100%);
      overflow: hidden;
      max-height: $desc-max-height;
      transition: 0.3s ease-in;
      opacity: 1;
      filter: brightness(1);
      // clip-path: polygon(0% 0%, 100% 0%, 100% 75%, 75% 75%, 75% 100%, 65% 75%, 0% 75%);
      .desc a {
        color: white;
      }
      // .ab-positioned {
      //   position: absolute;
      //   top: 100%;
      //   left: 0;
      //   right: 0;
      // }
    }
    .carousel--desc_hidden {
      opacity: 0;
      pointer-events: none;
    }
    .carousel--wrap_show {
      opacity: 1;
      transform: translate(0);
      transition: 0.3s ease-in;
    }
    .carousel--wrap_hidden {
      pointer-events: none;
      transition: $desc-transition;
      opacity: 0;
      transform: translateX(20px);
    }
  }
  .carousel--aside {
    @include shadow;
    background: $liner-gradient;

    flex: 1 0;
    padding-top: 1em;
    flex-direction: column;
    box-sizing: border-box;
    display: flex;
    border-radius: $aside-border-radius;
    // border: 1px dashed $dash-border-color;
    time {
      font-size: 12px;
      color: $small-text-color;
    }
    a {
      cursor: pointer;
      display: block;
      &.title {
        font-size: 14px;
        margin: 0.4em 0;
        color: $text-color;
        text-decoration: none;
        display: -webkit-box;
        overflow: hidden;
        text-overflow: ellipsis;
        -webkit-line-clamp: $max-line-number;
        -webkit-box-orient: vertical;
        &:hover {
          color: $text-hover-color;
          text-decoration: $text-underline-color underline;
        }
      }
      &.desc {
        font-size: 12px;
        font-weight: bold;
        color: $small-text-color;
        &:hover {
          color: $text-hover-color;
          text-decoration: $text-underline-color underline;
        }
      }
    }

    .carousel--item {
      flex: 1;
      display: flex;
      overflow: hidden;
      .wrapper {
        border-radius: 8px;
        flex: 1 1;
        overflow: hidden;
        margin: 0 1em 1em;
        padding: 0em 0.5em;
        transition: 0.1s ease-in;
        // border: 1px solid transparent;
      }
      .hover {
        cursor: pointer;
        background: $background-hover-color;
        // border: 0px solid $box-shadow-color !important;
        box-shadow: 0px 0px 4px $box-shadow-color;
      }
      &:not(:first-child) .wrapper {
        border-top: 1px dashed $dash-border-color;
      }
      &:first-child .wrapper {
        border-top: 1px dashed $dash-border-color;
      }
    }
  }
}
@media screen and (max-width: $screen-md) {
  .carousel {
    flex-direction: column;
    height: 800px;
    .carousel--main {
      height: 500px;
      margin: $margin 0;
      .carousel--desc {
        top: 100%;
        bottom: unset;
        padding: 1em 2em 1.5em;
      }
    }
    .carousel--aside {
      margin-top: calc(#{$desc-max-height} - #{$margin} + 0px);
      flex-direction: row;
      flex-wrap: wrap;
      -ms-flex-wrap: wrap;
      .carousel--item {
        flex: 1 1 33%;
      }
    }
  }
}
@media screen and (max-width: $screen-sm) {
  .carousel {
    .carousel--main .carousel--desc {
      font-size: 8px;
      height: 140px;
    }
    .carousel--aside .carousel--item {
      flex: 1 1 50%;
    }
  }
}
</style>
